              no-gutters
            >
              <v-col
                cols="3"
              >
                <v-card class="cardImage transparent" flat>
                  <v-img
                    @click="callInitialPage()"
                    src="../assets/img/logo_v.svg"
                  />
                </v-card>

              <div v-bind:class="{ 'mt-10': $vuetify.breakpoint.lgAndUp }">

                <div v-for="index in 6" :key="index">

                <menu-pluris
                  :id="index"
                  @callPage="callPageCard"
                  :title="$t('menu.item' + index)"
                  :items="index < 4 ? $t('menu.items' + index) : []"
                />

                </div>
              </div>

              </v-col>

              <v-divider
                class="divider"
                vertical
                inset
              />

              <v-col
